html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

*, *:before, *:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  font-family: Raleway;
  overflow-x: hidden;
  position: relative;
  background-color: #000000;
  color: #ffffff;
}

a {
  color: #000000;
  transition: color 0.3s ease-in-out;
  text-decoration: none;
}

a:hover {
  color: #ffffff;
}

strong {
  font-weight: 700;
}

canvas {
  position: fixed;
  z-index: 1;
}

main {
  position: relative;
  z-index: 5;
}

.container {
  width: 70%;
  margin: 0 auto;
  display: flex;
}

.title {
  font-weight: 700;
}

.insert {
  font-weight: 800;
  color: #edca00;
  margin-left: 0.25rem;
}

.link {
  font-size: calc(0.5rem + 0.75vw);
  line-height: calc(0.75rem + 0.75vw);
  text-align: center;
  width: 15rem;
  padding: 1.25rem 2rem;
  border: 1px solid #edca00;
  color: #edca00;
  transition: all 0.3s ease-in-out;
}

.link:active, .link:hover, .link:focus {
  cursor: pointer;
  background-color: rgba(237, 202, 0, 0.5);
  color: #ffffff;
}

.section {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20%;
  font-size: 2rem;
  line-height: 3rem;
  text-align: center;
  min-height: 100vh;
}

.section:nth-child(even) {
  background-color: black;
  color: #ffffff;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  border: none;
  padding: 1rem 1.5rem;
  border-radius: 3px;
  font-size: calc(0.25rem + 0.75vw);
  background-color: transparent;
  font-family: NewYork;
}

.btn--color {
  background-color: #FF0043;
  color: #ffffff;
}

.main-color {
  color: #FF0043;
  font-style: italic;
}

.header {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  align-items: center;
  padding: 1.5% 5%;
  margin: 0 auto;
  background-color: #FF0043;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5rem;
}

.logo__link {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  overflow: hidden;
  height: 3.5vh;
}

.logo__link svg,
.logo__link img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.logo__caption {
  display: flex;
  font-size: calc(0.25rem + 1vw);
  width: 100%;
  letter-spacing: 1.5px;
  color: #FF0043;
  font-weight: 700;
}

.logo__caption--bottom {
  justify-content: flex-end;
  text-align: right;
}

.navigation {
  width: 45%;
  display: flex;
  margin: 0 auto 0 2.5%;
}

.navigation__inner {
  display: flex;
  width: 100%;
  padding: 1rem 0;
  justify-content: space-between;
}

.navigation__item {
  display: flex;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.navigation__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 1rem;
  font-size: calc(0.25rem + 1vw);
}

.login-panel {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.login-panel__item:first-child {
  margin-right: 0.25rem;
}

.choose-language {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 7.5%;
}

.choose-language__item {
  display: flex;
  font-size: 1.25rem;
  border: none;
  background-color: transparent;
  position: relative;
  color: #ffffff;
  text-align: center;
  justify-content: center;
  align-items: center;
  padding: 0 0.5rem;
  transition: all 0.3s ease-in-out;
}

.choose-language__item:hover {
  cursor: pointer;
  color: #354a03;
}

.choose-language__item:nth-child(2n) {
  border-left: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
}

@media screen and (max-width: 1366px) {
  .choose-language {
    width: 10%;
  }
}

@media screen and (max-width: 1224px) {
  .choose-language {
    width: 12.5%;
  }
}

@media screen and (max-width: 1024px) {
  .choose-language {
    width: 17.5%;
  }
}

@media screen and (max-width: 768px) {
  .choose-language {
    width: 22.5%;
  }
}

@media screen and (max-width: 768px) {
  .choose-language {
    width: 30%;
  }
  .choose-language__item {
    width: calc(100% / 3);
  }
}

.hero {
  width: 100%;
  min-height: 100vh;
  padding: 5% 0;
  background-attachment: fixed;
  position: relative;
  display: flex;
  z-index: 2;
}

.hero--wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.hero__title {
  width: 100%;
  text-align: center;
  font-size: calc( 7.5rem + 0.5vw);
  line-height: calc( 8rem + 0.5vw);
}

.hero__description {
  width: 50%;
  font-size: calc(0.5rem + 0.75vw);
  line-height: calc(0.75rem + 0.75vw);
  text-align: justify;
}

.hero__btn {
  width: 50%;
  margin-top: 15%;
}

.hero__img {
  overflow: hidden;
}

.hero__img img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.about {
  width: 100%;
  margin: 0 auto;
  background: linear-gradient(-155deg, transparent 50%, white 50%);
  display: flex;
  padding: 10% 0;
}

.about--wrapper {
  justify-content: center;
  align-items: center;
}

.about__description {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 100%;
  padding: 5%;
  text-align: right;
  align-items: flex-end;
  mix-blend-mode: difference;
}

.about__img {
  width: 55rem;
  height: 32.5rem;
  background-image: url("../img/aboutus.jpg");
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  border-radius: 50%;
  transition: background 1s ease;
}

.about__img:hover {
  background-size: 110%;
  transition: background 1s ease;
}

.profile__title {
  font-size: calc(2.5rem + 0.75vw);
  line-height: calc(2.75rem + 0.75vw);
  width: 100%;
}

.profile__description {
  padding: 5rem 0;
  font-size: calc(0.5rem + 0.75vw);
  line-height: calc(0.75rem + 0.75vw);
  width: 100%;
}

.more-about {
  width: 100%;
  margin: 0 auto;
  background: linear-gradient(10deg, transparent 50%, white 50%);
  display: flex;
  position: relative;
  padding: 2.5% 0;
}

.more-about--wrapper {
  padding: 0 0 15%;
  flex-direction: column;
  align-items: center;
}

.more-about__title {
  font-size: calc(2rem + 0.75vw);
  line-height: calc(2.25rem + 0.75vw);
  display: flex;
  z-index: 2;
  color: #000000;
}

.more-about__description {
  padding: 2rem 0;
  z-index: 2;
  mix-blend-mode: difference;
  width: 60%;
  text-align: center;
  font-size: calc(0.35rem + 0.75vw);
  line-height: calc(0.5rem + 0.75vw);
}

.more-about__img {
  background-image: url("../img/me.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  width: 25rem;
  height: 25rem;
  border-radius: 50%;
  border-bottom-left-radius: initial;
  border-top-right-radius: initial;
  top: 50%;
  right: 0%;
  transform: translate(-50%, -50%);
  z-index: 1;
  transition: all 1s ease;
}

.portfolio {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.portfolio__title {
  width: 70%;
  margin: 0 auto;
  font-weight: 700;
  font-size: calc( 5rem + 0.5vw);
  line-height: calc( 6rem + 0.5vw);
  padding: 2.5% 0;
}

.works {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, auto);
  justify-content: center;
  align-items: center;
}

.works__item {
  width: 100%;
  height: 100%;
  padding: 10rem 5rem;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  background-image: url("../img/pexels-photo-1586144.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  cursor: none;
  filter: blur(1px);
  display: flex;
  flex-direction: column;
  transition: all 0.3s ease-in-out;
  justify-content: flex-end;
}

.works__item--show {
  opacity: 1;
}

.works__item:hover {
  filter: grayscale(0);
  color: red;
  z-index: 1;
}

.works__item:nth-child(even) {
  background-image: url("../img/pexels-photo-2228152.jpeg");
}

.works__item:nth-child(3n) {
  background-image: url("../img/pexels-photo-5091791.jpeg");
}

.work__title {
  font-size: calc(2rem + 0.25vw);
  line-height: calc(2rem + 0.25vw);
  width: 100%;
}

.work__description {
  padding: 2.5rem 0;
  text-align: right;
  font-size: calc(0.5rem + 0.7vw);
  line-height: calc(0.75rem + 0.7vw);
  width: 100%;
}

.work-2 {
  grid-column: 2/3;
  grid-row: 1/3;
}

.work-4 {
  grid-column: 1/2;
  grid-row: 2/4;
}

.work-6 {
  grid-column: 3/4;
  grid-row: 3/5;
}

.services {
  width: 100%;
  background-image: linear-gradient(35deg, yellow 50%, transparent 50%);
  display: flex;
  align-items: center;
  padding: 10% 0;
}

.services--wrapper {
  justify-content: space-between;
  align-items: center;
}

.services__title {
  font-size: calc(3.5rem + 0.75vw);
  line-height: calc(3.75rem + 0.75vw);
  mix-blend-mode: difference;
}

.box-group {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  list-style: none;
  text-align: center;
  mix-blend-mode: difference;
}

.box-group__item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2.5rem 0;
  width: 25%;
  opacity: 0;
  transform: translateY(30px);
  height: auto;
  transition: all 0.3s ease-in-out;
  mix-blend-mode: exclusion;
}

.box-group__item--show {
  opacity: 1;
}

.box-group__icon {
  background-position: center;
  background-size: 135%;
  background-repeat: no-repeat;
  width: 10rem;
  height: 10rem;
  border-radius: 50%;
  background-image: url("../img/service.jpg");
  transition: all 1s ease;
}

.box-group__icon:hover {
  transform: scale(1.2);
  background-size: 150%;
}

.box-group__caption {
  font-size: calc(0.5rem + 0.75vw);
  line-height: calc(0.75rem + 0.75vw);
  padding: 1.5rem 0;
}

.footer {
  width: 100%;
  padding: 10% 0;
  background-image: linear-gradient(-35.5deg, transparent 50%, yellow 50%);
  display: flex;
  align-items: center;
  position: relative;
  z-index: 5;
}

.footer--wrapper {
  height: 100%;
  justify-content: center;
  align-items: center;
  flex-direction: column-reverse;
  padding: 2.5% 0;
}

.footer__title {
  width: 100%;
  text-align: right;
  transform: translateX(7.5%);
  font-size: calc( 3rem + 0.5vw);
  line-height: calc( 4rem + 0.5vw);
}

.footer__linse {
  display: block;
  border-radius: 50%;
  background-color: #ffffff;
  width: 20rem;
  height: 20rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #fff;
  pointer-events: none;
  mix-blend-mode: difference;
}

.social-menu {
  display: flex;
  padding: 10% 0;
  justify-content: center;
  align-items: center;
  mix-blend-mode: exclusion;
}

.social-menu__link {
  display: flex;
  font-size: 0;
  transition: all 0.3s ease-in-out;
  border-radius: 50%;
  opacity: 0;
  width: 10rem;
  height: 10rem;
  margin: 0 1.5rem;
}

.social-menu__link > svg {
  border-radius: 50%;
  width: 100%;
  transform: scale(1);
  background-color: #FF0043;
}

.social-menu__link:hover {
  cursor: pointer;
  opacity: 1;
  transform: scale(1.25);
}

.social-menu__link:hover .footer__linse {
  transform: scale(1.5);
}

